<template>
  <div class="card">
    <div class="card-header">
      Exam07ComputedProperties
    </div>
    <div class="card-body">
      <p>name: {{name}}</p>
      <p>ssn1: {{ssn1}}</p>
      <p>ssn2: {{ssn2}}</p>
      <p>ssn: {{ssn1}}-{{ssn2.charAt(0) + "******"}}</p> <!--주민번호 앞만 나오고. 뒷자리는 한 자리만 보이게 하고 나머지는 * 처리-->
      <p>ssn: {{getSsn()}}</p>
      <p>ssn: {{computedSsn}}</p>
      <hr/>
      <button class="btn btn-info btn-sm mr-2" @click="changeName">name 속성 변경</button> <!--이름 변경-->
      <button class="btn btn-info btn-sm mr-2" @click="changeSsn2">ssn2 속성 변경</button> <!--ssn 변경-->
    </div>
  </div>   
</template>

<script setup>
import {ref, computed} from "vue";

//반응형 속성 선언
const name=ref("홍길동");
const ssn1=ref("880515");
const ssn2=ref("1110345");
const computedSsn=computed(()=>{
  const ssn=ssn1.value+"-"+ssn2.value.charAt(0)+"******";
  console.log("computed callback 실행");
  return ssn;
});
//메소드 선언
function getSsn() {
  const ssn=ssn1.value+"-"+ssn2.value.charAt(0)+"******";
  console.log("getSsn() 실행");
  return ssn;
}
function changeName() {
  name.value+="동";
}
function changeSsn2() {
  ssn2.value+="2";
}
</script>

<style scoped>

</style>