<template>
<div class="card">
  <div class=card-header>
      Exam06ListRendering
  </div>
  <div class="card-body">
    <div>
        <h6>범위 반복</h6>
        <span v-for="n in 5" :key="n" class="mr-2">
            <img :src="require(`@/assets/photos/photo${n}.jpg`)" width="150" height="150"/>
        </span>
    </div>
    <div class="mt-3">
        <h6>배열 항목 반복</h6> <!--배열의 항목 수 만큼 반복할거야-->
        <span v-for="(item, index) in photos" :key="index" class="mr-2">
